import { Injectable } from '@angular/core';

declare class Visualforce {
  static remoting: { Manager: { invokeAction: any } };
}

@Injectable()
export class RemoteActionsService {

  private callRemote(methodName, params, resolve, reject) {
    console.log(params);
    Visualforce.remoting.Manager.invokeAction(
      methodName,
      ...params,
      function (result, event) {
        console.log('event');
        console.log(event);
        console.log('result:');
        console.log(result);

        if (event.status) {
          resolve(result);
        }
      },
      {} //Options I am not setting
    );
  }

  constructor() { }

  getContacts() {
    console.log('getContacts called');

    return new Promise((resolve, reject) => {
      this.callRemote('AngularController.GetContacts', [], resolve, reject);
    })

  }

  getContact(id: string): Promise<{ Id: string, Name: string, Email: string }> {
    console.log('getContact called');

    return new Promise((resolve, reject) => {
      this.callRemote('AngularController.GetContact', [id], resolve, reject);
    })
  }
}


